import React, { useEffect } from 'react';

export default function SearchSite() {
  const searchRef = React.createRef();

  //   ,  useEffect          .

  useEffect(() => {
    searchRef.current.focus();
  }, []);

  //

  return (
    <input ref={searchRef} type="text" className="search-site" placeholder="  " title="    " />
  );
}
